<template>
  <div class="pa-4">
    <v-btn
      class="ma-2"
      @click="exportToExcel()"
    >
      Export to Excel
    </v-btn>
    <v-btn
      class="ma-2"
      @click="exportToCsv()"
    >
      Export to Csv
    </v-btn>
    <ag-grid-vue
      style="height: 65vh;"
      class="ag-theme-alpine"
      :column-defs="columnDefs"
      :row-data="rowData"
      :grid-options="gridOptions"
      :excel-styles="excelStyles"
      @first-data-rendered="onFirstDataRendered"
    />
    <p><b>注意：</b>长数字导出excel，在excel显示为科学计数的问题</p>
    <p>对于长数字 excel、csv数字转为字符串，即要表头定义<code>cellClass</code>为<code>stringType</code>然后再导出，就没问题</p>
    <p>浮点数位数字多于15位，已经超出了 JavaScript 精确浮点数范围 -1.7976931348623157e+308 至 1.7976931348623157e+308 （即正负2的1024次方）</p>
    <p>整数位数字多于16位，已经超出了 JavaScript 精确整数范围 -9007199254740992 至 9007199254740992 （即正负2的53次方）</p>
    <p>或者已经因为过长的数字而显示为科学计数的，要让数据来源为字符串，否则无法保证数字精度和导出的excel为正常数字</p>
    <p>
      JavaScript 提供Number对象的MAX_VALUE和MIN_VALUE属性，返回可以表示的具体的最大值和最小值。
      <br>
      <code>Number.MAX_VALUE // 1.7976931348623157e+308</code>
      <br>
      <code>Number.MIN_VALUE // 5e-324</code>
    </p>
    <p>
      参考文档<br>
      <a href="https://www.ag-grid.com/angular-data-grid/excel-export-data-types/">https://www.ag-grid.com/angular-data-grid/excel-export-data-types/</a>
      <br>
      <a href="https://wangdoc.com/javascript/types/number#%E6%95%B0%E5%80%BC%E7%B2%BE%E5%BA%A6">JavaScript 数值精度</a>
    </p>
  </div>
</template>

<script>
import { LicenseManager } from 'ag-grid-enterprise';
import { AgGridVue } from 'ag-grid-vue';

// 挖槽 不去注册的话，exportDataAsExcel不能用
LicenseManager.setLicenseKey('CompanyName=Suzhou ruhlamat Smart Technologies Co., Ltd.,LicensedApplication=Infinity,LicenseType=SingleApplication,LicensedConcurrentDeveloperCount=1,LicensedProductionInstancesCount=30,AssetReference=AG-030422,SupportServicesEnd=30_August_2023_[v2]_MTY5MzM1MDAwMDAwMA==28423dd515d527503fc5a257dc86a8ac');

export default {
  components: {
    AgGridVue,
  },
  data() {
    return {
      columnDefs: [
        { field: 'name' },
        {
          field: 'desc',
          valueFormatter: (params) => { // 只是修改显示的文字，不改变表格数据，导出也不影响
            if (!params?.value) {
              return '<无描述>';
            }
            return params.value;
          },
        },
        {
          field: 'number',
          cellClass: 'stringType', // 配合excelStyles，这样导出的excel就不是科学计数了
          // valueGetter: arg => { // 改变表格显示的文字
          //   const num = arg.data.number;
          //   return num.toString() + '\t' + '@@@'
          // },
        },
        {
          field: 'text',
          cellClass: 'stringType', // 配合excelStyles，这样导出的excel就不是科学计数了
        },
        {
          field: 'action',
          cellRenderer: (params) => {
            return `<button style="border: 1px solid;border-radius: 5px;">${params.value}</button>`;
          },
        },
      ],
      excelStyles: [
        {
          id: 'stringType',
          dataType: 'String',
        },
      ],
      rowData: [
        { name: '一般整数', desc: '', number: 72000, text: '带中文数字123456789012334243433', action: 'test' },
        { name: '测试带小数位', desc: '', number: 35000.231231231, text: '' },
        { name: '带.00小数', desc: '', number: 32000.00, text: 'engnumber12345678901234567890' },
        { name: '测试长整数', desc: '16位', number: 1234567890123456, text: '1234567890123456' },
        { name: '极限-整数', desc: '16位', number: -9007199254740992, text: '-9007199254740992' },
        { name: '极限整数', desc: '16位', number: 9007199254740992, text: '9007199254740992' },
        { name: '极限整数+1', desc: '16位', number: 9007199254740993, text: '9007199254740993' },
        { name: '测试长整数', desc: '17位', number: 12345678901234567, text: '12345678901234567' },
        { name: '测试长整数', desc: '18位', number: 123456789012345678, text: '123456789012345678' },
        { name: '测试长整数', desc: '19位', number: 1234567890123456789, text: '12345678901234567890' },
        { name: '测试长整数', desc: '20位', number: 12345678901234567890, text: '12345678901234567890' },
        { name: '测试长整数', desc: '21位', number: 123456789012345678901, text: '123456789012345678901' },
        { name: '测试长整数', desc: '30位', number: 123456789012345678901234567890, text: '123456789012345678901234567890' },
      ],
      gridOptions: {
        defaultColDef: {
          sortable: true,
          filter: true,
          resizable: true,
        },
      },
    };
  },
  methods: {
    onFirstDataRendered(params) {
      this.gridApi = params.api;
      this.gridColumnApi = params.columnApi;
    },
    exportToExcel() {
      this.gridApi.exportDataAsExcel();
    },
    exportToCsv() {
      this.gridApi.exportDataAsCsv();
    }
  }
}
</script>

<style scoped lang="scss">
@import "~ag-grid-community/styles/ag-grid.css";
@import "~ag-grid-community/styles/ag-theme-alpine.css";
</style>
